.arrow {
    width: 50px;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.7);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }
  
  .next {
    right: 10px;
    background-color: rgb(161, 37, 37);
    
  }
  
  .prev {
    left: 10px;
    background-color: rgb(161, 37, 37);
  }
  
.text-slider {
  width: 100vw; 
  overflow: hidden;
  position: relative;
 
}

.text-item {
  border: 2px solid black;
  border-radius: 50px 50px 50px 50px;
  width: 50vw;
  display: none;

  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  text-align: center;
  padding: 30px;
  
}

.text-item.active {
  display: block;
  opacity: 1;
}
