/* WebsiteBody.css */
.website-body {
    text-align: center;
    padding: 20px;
    background-color: #cad3d7;
    color: rgba(110, 6, 24, 0.866);
    height: 80vh;
    align-items: center;
  }
  
  h1 {
    color: #333;
  }
  
  a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
    margin-top: 10px;
    display: inline-block;
  }
  
  a:hover {
    text-decoration: underline;
  }
  .slider {
    /* ImageSlider.css */




  
    img{
      margin: auto;
    width: 50vw;
    height: 500px;
    align-items: center;
  }
}
  @media screen and (max-width:360px) {
    .slider {
      /* ImageSlider.css */
  
  margin-top: 20px;
  
  
    
      img{
       
        margin: auto;
        margin-left: 10px;
      width: 93vw;
      height: 500px;
      align-items: center;
    }
  }
    
  }
