// Navbar.scss
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #000000;
  color: white;

  .logo {
    height: 10vh;
    display: flex;
    font-size: 1.5rem;
 margin-left: 20px;
 h1{
  font-size: 23px;
  align-items: center;
  margin-top: 30px;
  margin-left: 10px;
 }

    img {
      width: 130px;
      height: 100%;
     
    }
  }

  .menu-button {
    cursor: pointer;
    img {
      width: 50px;
      height: 50px;
      border-radius: 60px 60px 60px 60px;
    }
  }

  .menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh; /* Start with height 0 */
    background-color: rgba(0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 20px;
    align-items: center;
    transition: height 0.3s ease-in-out;
    animation: slide 01s ease-in-out forwards; /* Apply the animation */

    &.menu-open {
      height: 100%; /* Set height to reveal the menu */
      
    }

    .cancel-button {
      position: absolute;
      right: 60px;
      cursor: pointer;
      

      img {
        width: 50px;
        height: 50px;
        border-radius: 80px 80px 80px 80px;
      }
    }

    .menu-body {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: auto;
      .social-media-links {
        margin-top: 20px;
        align-items: center;
        display: flex;
        flex-direction: column;
      
    
        a {
            margin: 10px 4px;
            color: #ed0303;
            h2{
              font-size: 19px;
            }
          }
          img{
            width: 70px;
            height: 70px;
            border-radius: 60px 60px 60px 60px;
          }
      }

      a {
        color: white;
        text-decoration: none;
        margin-bottom: 10px;
        font-size: 1.2rem;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

@keyframes slide {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes slide1 {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}